/* frontend/src/styles/theme-variables.css - auto-generat din theme.ts */

:root {
  /* COLORS: PRIMARY */
  --color-primary-50: #f0fdf4;
  --color-primary-100: #dcfce7;
  --color-primary-200: #bbf7d0;
  --color-primary-300: #86efac;
  --color-primary-400: #4ade80;
  --color-primary-500: #16a34a;
  --color-primary-600: #15803d;
  --color-primary-700: #166534;
  --color-primary-800: #14532d;
  --color-primary-900: #052e16;

  /* COLORS: SECONDARY */
  --color-secondary-50: #f8fafc;
  --color-secondary-100: #f1f5f9;
  --color-secondary-200: #e2e8f0;
  --color-secondary-300: #cbd5e1;
  --color-secondary-400: #94a3b8;
  --color-secondary-500: #334155;
  --color-secondary-600: #1e293b;
  --color-secondary-700: #0f172a;
  --color-secondary-800: #020617;
  --color-secondary-900: #020617;

  /* COLORS: SUCCESS */
  --color-success-50: #ecfdf5;
  --color-success-100: #d1fae5;
  --color-success-200: #a7f3d0;
  --color-success-300: #6ee7b7;
  --color-success-400: #34d399;
  --color-success-500: #059669;
  --color-success-600: #047857;
  --color-success-700: #065f46;
  --color-success-800: #064e3b;
  --color-success-900: #022c22;

  /* COLORS: WARNING */
  --color-warning-50: #fffdf7;
  --color-warning-100: #fffaeb;
  --color-warning-200: #fef3c7;
  --color-warning-300: #fde68a;
  --color-warning-400: #fcd34d;
  --color-warning-500: #f59e0b;
  --color-warning-600: #d97706;
  --color-warning-700: #b45309;
  --color-warning-800: #92400e;
  --color-warning-900: #78350f;

  /* COLORS: ERROR */
  --color-error-50: #fef2f2;
  --color-error-100: #fee2e2;
  --color-error-200: #fecdd3;
  --color-error-300: #fca5a5;
  --color-error-400: #f87171;
  --color-error-500: #dc2626;
  --color-error-600: #991b1b;
  --color-error-700: #7f1d1d;
  --color-error-800: #650a0a;
  --color-error-900: #450a0a;

  /* COLORS: GRAY */
  --color-gray-50: #fafaf9;
  --color-gray-100: #f5f5f4;
  --color-gray-200: #e7e5e4;
  --color-gray-300: #d6d3d1;
  --color-gray-400: #a8a29e;
  --color-gray-500: #78716c;
  --color-gray-600: #57534e;
  --color-gray-700: #44403c;
  --color-gray-800: #292524;
  --color-gray-900: #1c1917;

  /* COLORS: WHITE */
  --color-white: #ffffff;

  /* COLORS: BLACK */
  --color-black: #000000;


  /* SPACING */
  --spacing-0: 0;
  --spacing-xs: 0.25rem;
  --spacing-sm: 0.5rem;
  --spacing-md: 1rem;
  --spacing-lg: 1.5rem;
  --spacing-xl: 2rem;
  --spacing-2xl: 3rem;
  --spacing-3xl: 4rem;
  --spacing-4xl: 6rem;


  /* SHADOWS */
  --shadow-none: none;
  --shadow-sm: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
  --shadow-md: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  --shadow-xl: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);


  /* BORDER RADIUS */
  --radius-none: 0;
  --radius-sm: 0.125rem;
  --radius-md: 0.375rem;
  --radius-lg: 0.5rem;
  --radius-xl: 0.75rem;
  --radius-full: 9999px;


  /* TYPOGRAPHY: FONT FAMILY */
  --font-family-sans: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  --font-family-mono: Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;

  /* TYPOGRAPHY: FONT SIZE */
  --font-size-xs: 0.75rem;
  --font-size-sm: 0.875rem;
  --font-size-md: 1rem;
  --font-size-lg: 1.125rem;
  --font-size-xl: 1.25rem;
  --font-size-2xl: 1.5rem;
  --font-size-3xl: 1.875rem;

  /* TYPOGRAPHY: FONT WEIGHT */
  --font-weight-light: 300;
  --font-weight-normal: 400;
  --font-weight-medium: 500;
  --font-weight-semibold: 600;
  --font-weight-bold: 700;

  /* TYPOGRAPHY: LINE HEIGHT */
  --line-height-tight: 1.25;
  --line-height-normal: 1.5;
  --line-height-relaxed: 1.75;


  /* BREAKPOINTS */
  --breakpoint-sm: 640px;
  --breakpoint-md: 768px;
  --breakpoint-lg: 1024px;
  --breakpoint-xl: 1280px;
  --breakpoint-2xl: 1536px;
  
  /* Z-INDEX STACK */
  --z-index-0: 0;
  --z-index-10: 10;
  --z-index-20: 20;
  --z-index-30: 30;
  --z-index-40: 40;
  --z-index-50: 50;
  --z-index-auto: auto;
  
  /* GRADIENTS */
  --gradient-primary: linear-gradient(to right, var(--color-primary-500), var(--color-primary-600));
  --gradient-secondary: linear-gradient(to right, var(--color-secondary-500), var(--color-secondary-600));
  --gradient-success: linear-gradient(to right, var(--color-success-500), var(--color-success-600));
  --gradient-warning: linear-gradient(to right, var(--color-warning-500), var(--color-warning-600));
  --gradient-error: linear-gradient(to right, var(--color-error-500), var(--color-error-600));
  
  /* ANIMATIONS */
  --animation-duration-fast: 150ms;
  --animation-duration-normal: 300ms;
  --animation-duration-slow: 500ms;
  --animation-timing-ease: ease;
  --animation-timing-ease-in: ease-in;
  --animation-timing-ease-out: ease-out;
  --animation-timing-ease-in-out: ease-in-out;
  
  /* TOKENS SPECIFICE APLICAȚIEI */
  --token: 1rem;
  --token-xs: 0.25rem;
  --token-sm: 0.5rem;
  --token-md: 1rem;
  --token-lg: 1.5rem;
  --token-xl: 2rem;
  
  --border-token: 1px solid var(--color-secondary-200);
  --shadow-token: var(--shadow-md);
  --radius-token: var(--radius-lg);
  --radius-token-lg: var(--radius-xl);
}