/* Importă variabilele CSS și stilurile de componente din sistemul de design tokens */
@import './styles/theme-variables.css';
@import './styles/theme-components.css';

@tailwind base;
@tailwind components;
@layer components {
  /* Utilitar global pentru eliminarea outline-ului la focus pe tab/NavLink */
  .fx-no-outline {
    outline: none !important;
    box-shadow: none !important;
    --tw-ring-shadow: 0 0 #0000 !important;
    --tw-ring-offset-shadow: 0 0 #0000 !important;
    --tw-ring-color: transparent !important;
    --tw-ring-offset-width: 0px !important;
  }

    .btn {
      @apply px-4 py-2 rounded font-medium focus:outline-none focus:ring-2 focus:ring-opacity-50 transition-colors;
    }
    .btn-primary {
      @apply bg-primary-500 text-white hover:bg-primary-600 focus:ring-primary-300;
    }
    .btn-secondary {
      @apply bg-secondary-500 text-white hover:bg-secondary-600 focus:ring-secondary-300;
    }
    .btn-accent {
      @apply bg-warning-500 text-white hover:bg-warning-600 focus:ring-warning-300;
    }
    .input-field {
      @apply px-3 py-2 border border-gray-300 rounded focus:outline-none focus:ring-2 focus:ring-primary-300 focus:border-transparent;
    }
    .table-cell {
      @apply px-3 py-2 text-sm text-gray-800;
    }
    .table-header {
      @apply px-3 py-2 text-left text-xs font-medium text-gray-600 uppercase tracking-wider;
    }
    /* Utilități pentru tabele Excel-like */
    .excel-cell {
      @apply border border-gray-200 p-1 min-w-[80px] outline-none;
    }
    .excel-cell-selected {
      @apply bg-primary-100 ring-2 ring-primary-500;
    }
    .excel-header {
      @apply bg-gradient-to-r from-secondary-700 to-secondary-800 text-white font-medium sticky top-0 z-10;
    }
    /* Noi clase pentru accent gold */
    .text-accent {
      @apply text-warning-500;
    }
    .bg-accent {
      @apply bg-warning-500;
    }
    .border-accent {
      @apply border-warning-500;
    }
  
    /* Utilitare noi bazate pe tokens */
    .shadow-token {
      @apply shadow-md;
    }
    .rounded-token {
      @apply rounded-lg;
    }
    .p-token {
      @apply p-4;
    }
  }
@tailwind utilities;

/* Animații personalizate pentru efecte vizuale */
@keyframes shimmer {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(100%);
  }
}

.animate-shimmer {
  animation: shimmer 2s infinite;
}
